<script>
import BrandFeaturedItemsSection from '../../components/BrandFeaturedItemsSection.vue'
import BrandItemListSection from '../../components/BrandItemListSection.vue'

export default {
	components: {
		BrandFeaturedItemsSection,
		BrandItemListSection
	},
	data() {
		return {
			tabItems: [
				{ label: "设计师", active: false },
				{ label: "优服务", active: false },
				{ label: "品牌馆", active: true },
			],
		}
	},
	methods: {
		goBack() {
			uni.navigateBack()
		},
		switchTab(index) {
			this.tabItems.forEach((tab, i) => {
				tab.active = i === index
			})
			
			// 跳转到对应页面
			if (index === 0) {
				uni.redirectTo({
					url: '/pages/designer/index'
				})
			} else if (index === 1) {
				uni.redirectTo({
					url: '/pages/service/index'
				})
			}
		}
	}
}
</script>

